	<div>

	<div class="container" :style='{"minHeight":"100vh","width":"100%","alignItems":"center","background":"url(http://codegen.caihongy.cn/20240201/16ba3d6813174f3ca7315ed484ce0d90.png) center center/100% 100% no-repeat","justifyContent":"center","display":"flex"}'>
