			<div v-if="true" :style='{"margin":"0 0 10px 0","color":"#000","textAlign":"center","width":"100%","lineHeight":"44px","fontSize":"30px","textShadow":"none","fontWeight":"bold"}'></p></div>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="yonghuzhanghao">
				<div v-if="true" :style='{"width":"110px","lineHeight":"44px","fontSize":"14px","color":"#000","background":"none"}' :class="changeRules('yonghuzhanghao')?'required':''"></div>
				<el-input v-model="registerForm.yonghuzhanghao"  placeholder="" />
			</el-form-item>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="mima">
				<div v-if="true" :style='{"width":"110px","lineHeight":"44px","fontSize":"14px","color":"#000","background":"none"}' :class="changeRules('mima')?'required':''"></div>
				<el-input v-model="registerForm.mima" type="password" placeholder="" />
			</el-form-item>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="mima2">
				<div v-if="true" :style='{"width":"110px","lineHeight":"44px","fontSize":"14px","color":"#000","background":"none"}' :class="changeRules('mima')?'required':''"></div>
				<el-input v-model="registerForm.mima2" type="password" placeholder="" />
			</el-form-item>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="yonghuxingming">
				<div v-if="true" :style='{"width":"110px","lineHeight":"44px","fontSize":"14px","color":"#000","background":"none"}' :class="changeRules('yonghuxingming')?'required':''"></div>
				<el-input v-model="registerForm.yonghuxingming"  placeholder="" />
			</el-form-item>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="xingbie">
				<div v-if="true" :style='{"width":"110px","lineHeight":"44px","fontSize":"14px","color":"#000","background":"none"}' :class="changeRules('xingbie')?'required':''"></div>
                <el-select v-model="registerForm.xingbie" placeholder="" >
                  <el-option
                      v-for="(item,index) in yonghuxingbieOptions"
                      :key="index"
                      :label="item"
                      :value="item">
                  </el-option>
                </el-select>
			</el-form-item>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="yonghushouji">
