				<div v-if="true" :style='{"width":"110px","lineHeight":"44px","fontSize":"14px","color":"#000","background":"none"}' :class="changeRules('touxiang')?'required':''"></div>
                <file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:multiple="true"
					:fileUrls="registerForm.touxiang?registerForm.touxiang:''"
					@change="yonghutouxiangUploadChange"
				></file-upload>
			</el-form-item>
			<el-form-item :style='{"width":"80%","padding":"0","margin":"0 auto 15px","height":"auto"}' v-if="tableName=='yonghu'" prop="shenfenzheng">
