			<el-button :style='{"border":"0","cursor":"pointer","padding":"0 10px","boxShadow":"0 0 6px rgba(64, 158, 255, .5)","margin":"20px auto 5px","color":"#fff","display":"none","outline":"none","borderRadius":"8px","background":"rgba(64, 158, 255, 1)","width":"80%","fontSize":"16px","height":"44px"}' @click="resetForm('registerForm')"></el-button>
			<router-link :style='{"cursor":"pointer","padding":"10px 10%","color":"rgba(159, 159, 159, 1)","display":"block","lineHeight":"1","fontSize":"14px","textDecoration":"none"}' to="/login"></router-link>
			<div class="idea1" :style='{"width":"100%","background":"red","display":"none","height":"40px"}'></div>
